.page-404 .homepage-link {
  background-color: #17fcc4;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: scale(1);
  transition: transform 0.3s ease;
  position: absolute;
}
.page-404 .homepage-link:hover, .page-404 .homepage-link:focus {
  transform: scale(1.1);
  transition: transform 0.3s ease;
}
